<template>
  <div class="home">
    <b-modal id="modal-1" ref="my-modal" v-if="data_diploma.results" title="Diplomni biriktirish">
      <p class="my-4">Diplom kodi:{{data_diploma.results[0].unique_code}}</p>
      <p>Diplom olgadigan shaxs:{{options_data.result[0].athletename}}</p>

      <div class="text-center">
        <button class="btn_search active" @click="putDiploma()">
          <span v-if="!spin1" >Saqlash</span>
          <b-spinner variant="white" v-if="spin1" ></b-spinner>
        </button>
      </div>
    </b-modal>
    <Sidebar :user_name="user_id.username"></Sidebar>
    <div class="home_calass">
        <div class="home_main">
          <div class="data_home">Ma’lumotlarni kiriting</div>
            <div class="home_forms">
              <form @submit.prevent="postDiploma()">
                <div class="form_home">
                <div>
                  <div class="login_forms mx-1" >
                    <input type="text" placeholder="AA" v-model="documentseries" @input="documentseries = documentseries.toUpperCase()" maxlength="2">
                  </div>
                  <div class="login_forms mx-1" >
                    <input type="number" placeholder="Passport raqami" v-model="documentnumber"  >
                  </div>
                </div>
                  <div>
                    <div class="login_forms mx-1">
                      <input type="date" v-model="dateofbirth">
                    </div>
                    <div class="login_forms mx-1">
                      <input type="number" placeholder="Diplom kode" v-model="diplom_code">
                    </div>
                  </div>
                </div>
                <div class="text-center">
                  <button class="btn_search " :class="{'active':diplom_code&&dateofbirth&&documentnumber&&documentseries}">
                    <span v-if="!spin">Izlash</span>
                    <b-spinner variant="white" v-if="spin" ></b-spinner>
                  </button>
                </div>
              </form>
            </div>
        </div>
    </div>
    <div class="passports" v-if="options_data.result">
      <div class="passports_child">
        <div class="icons">
            <div class="logo_f">
              <div class="main_logo">
                <img src="../assets/diplom/111.png" alt="">
              </div>
            </div>
            <div class="logos_user">
              <img src="../assets/logos.jpg" alt="">
            </div>
          <div class="user_passport">
            Passport raqami:
          </div>
          <div class="user_passport numbers">

            {{options_data.result[0].documentseries}}

            {{options_data.result[0].documentnumber}}
          </div>
        </div>
        <div class="data_user" >
          <div>
            <h4>F.I.O</h4>
          </div>
          <div class="user_name">
                {{options_data.result[0].athletename}}
          </div>
          <div>
            <h4>Viloyati</h4>
          </div>
          <div class="user_name">
            {{options_data.result[0].oblastname}}
          </div>
          <div>
            <h4>Tumani</h4>
          </div>
          <div class="user_name">
              {{options_data.result[0].regionname}}
          </div>
          <div>
            <h4>Mahallasi</h4>
          </div>
          <div class="user_name">

            {{options_data.result[0].mfyname}}
          </div>
          <div>
            <h4>Sektor</h4>
          </div>
          <div class="user_name">

            {{options_data.result[0].regionsectorname}}
          </div>
          <div>
            <h4>Diplom kodi</h4>
          </div>
          <div class="user_name" v-if="data_diploma.results">
            {{data_diploma.results[0].unique_code}}
          </div>
        </div>
      </div>
    </div>

    <div class="text-center my-4" v-if="options_data.result">
      <button class="btn btn-outline-primary" v-b-modal.modal-1>
        <span>Tasdiqlash</span>
      </button>
    </div>

  </div>
</template>
<script>
import Sidebar from "../components/Sidebar";
export default {
  data () {
    return {
      user_id:'',
      diplom_code:'',
      dateofbirth:'',
      documentnumber:'',
      documentseries:'',
      spin:false,
      spin1:false,
      options: [
      ],
      item:'',
      options2: [
      ],
      item2: '',
      options_data:[],
      data_diploma:[],
      pages:1,

    }
  },
  mounted() {
    this.base_url.get(`/users/me/`, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        },
    )
        .then(res=>{
          this.user_id=res.data
      })
  },
  methods: {
    postDiploma(){

    this.base_url.post(`/diplomas/get_winner/`,
        {
        "documentseries": this.documentseries,
          "documentnumber":this.documentnumber,
          "dateofbirth":new Date( this.dateofbirth).toLocaleDateString()
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        },
          )
        .then(res=>{

          if(res.data.Success==true){
            console.log(res.data)
            this.base_url.get(`diplomas?code=${this.diplom_code}`)
                .then(responce=>{
                    this.data_diploma=responce.data
                  this.options_data=res.data

                })
            .catch(()=>{

              this.$toast("Diplom kodi xato kiritilgan!", {
                timeout: 2000,
                type:"error"
              });
            })
          }
          if(res.data.Success==false){
            this.$toast("Bu ma'lumotlik ishtirokchi topilmadi", {
              timeout: 2000,
              type:"error"
            });
          }
          if(res.data.result.length==0) {
            this.$toast("Diplom kodi yoki diplom egasi ma'lumotlari xato kiritilgan", {
              timeout: 2000,
              type:"error"
            });

          }
        })
      .catch(()=>{

        this.$toast("Diplom biriktirishda  xatolik bor!", {
          timeout: 2000,
          type:"error"
        });
      })
    },
    putDiploma(){
      this.base_url.patch(`/diplomas/${this.data_diploma.results[0].id}/?owner=${this.user_id.id}`,{
        "full_name":this.options_data.result[0].athletename
      },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            },
          }
      )
      .then(()=>{
        this.$toast("Muofaqqiyatlik biriktirildi", {
          timeout: 2000,
          type:"success"
        });
        this.$refs['my-modal'].hide()

            this.diplom_code=''
            this.dateofbirth=''
            this.documentnumber=''
            this.documentseries=''
              this.options_data=[]
        this.data_diploma=[]

      })

      .catch(()=>{
        this.$toast("Xatolik bor", {
          timeout: 2000,
          type:"error"
        });
      })
    },
    reset () {
      this.item = {}
    },
    selectFromParentComponent1 () {
      // select option from parent component
      this.item = this.options[0]
    },
    reset2 () {
      this.item2 = ''
    },
    selectFromParentComponent2 () {
      // select option from parent component
      this.item2 = this.options2[0].value
    }
  },
  components: {
    Sidebar,
  }
}
</script>
